/*
 * react-circular-progressbar styles
 *
 * All of the styles in this file are optional and configurable!
 */

.CircularProgressbar {
    /*
     * This fixes an issue where the CircularProgressbar svg has
     * 0 width inside a "display: flex" container, and thus not visible.
     *
     * If you're not using "display: flex", you can remove this style.
     */
    width: 100%;
}

.CircularProgressbar .CircularProgressbar-path {
    stroke: #3e98c7;
    stroke-linecap: round;
    -webkit-transition: stroke-dashoffset 0.5s ease 0s;
    -o-transition: stroke-dashoffset 0.5s ease 0s;
    transition: stroke-dashoffset 0.5s ease 0s;
}

.CircularProgressbar .CircularProgressbar-trail {
    stroke: #d6d6d6;
}

.CircularProgressbar .CircularProgressbar-text {
    fill: #3e98c7;
    font-size: 20px;
    dominant-baseline: middle;
    text-anchor: middle;
}

.progressbar-red .CircularProgressbar-path { stroke: red; }
.progressbar-blue .CircularProgressbar-path { stroke: blue; }
.progressbar-green .CircularProgressbar-path { stroke: green; }
.progressbar-yellow .CircularProgressbar-path { stroke: yellow; }
.progressbar-orange .CircularProgressbar-path { stroke: orange; }
.progressbar-purple .CircularProgressbar-path { stroke: purple; }

.progressbar-red .CircularProgressbar-text { fill: red; }
.progressbar-blue .CircularProgressbar-text { fill: blue; }
.progressbar-green .CircularProgressbar-text { fill: green; }
.progressbar-yellow .CircularProgressbar-text { fill: yellow; }
.progressbar-orange .CircularProgressbar-text { fill: orange; }
.progressbar-purple .CircularProgressbar-text { fill: purple; }


